<template>
  <el-card class="box-card">
    <div style="margin-top: 5px;">
<!--      搜索添加区域-->
      <el-row :gutter="24">
        <!-- <el-col :span="3">
          <el-input v-model="queryinfo.m_id" clearable placeholder="Browser Version" @keyup.enter.native="search"></el-input>
        </el-col>
        <el-col :span="3">
          <el-input v-model="queryinfo.client_category" clearable placeholder="System category" @keyup.enter.native="search"></el-input>
        </el-col>
        <el-col :span="2">
          <el-input v-model="queryinfo.m_name" clearable placeholder="User Agent " @keyup.enter.native="search"></el-input>
        </el-col>
        <el-col :span="2">
          <el-input v-model="queryinfo.system_category" clearable placeholder="Operating system" @keyup.enter.native="search"></el-input>
        </el-col> -->
        <el-col :span="13" class="buttonlist">
          <!-- <el-button type="primary" @click="search">Search</el-button> -->
          <el-button type="primary" @click="dialogVisible = true">新增</el-button>
          <el-button type="danger" @click="delAll()">批量删除</el-button>
        </el-col>
      </el-row>
<!--      列表内容区域-->
      <el-table :data="tasklist" v-loading="loading" size="medium" stripe border style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="index"></el-table-column>
          <el-table-column
            prop="m_id"
            label="主任务ID">
          </el-table-column>
          <el-table-column
            prop="m_name"
            label="主任务">
          </el-table-column>
          <el-table-column
            prop="s_id"
            label="从任务ID">
          </el-table-column>
          <el-table-column
            prop="s_name"
            label="从任务">
          </el-table-column>
          <el-table-column label="Operation" width="200">
            <template slot-scope="scope">
              <el-button size="small" type="danger" icon="el-icon-delete" @click="dele(scope.row.id)"></el-button>
            </template>
          </el-table-column>
        </el-table>
<!--      分页区域-->
      <!-- <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 50, 100]"
        :page-size="queryinfo.page_size"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
        :current-page.sync="currentPage">
      </el-pagination> -->
<!--      添加主机对话框-->
      <el-dialog title="新增" :visible.sync="dialogVisible" width="50%"
       @close="dialogClose" :close-on-click-modal="false">
<!--        表单-->
      <el-form :model="addForm" label-width="150px" :rules="addFormRule" ref="addFormRef" >
        <el-form-item label="主任务ID:" prop="m_id">
          <el-input v-model="addForm.m_id"></el-input>
        </el-form-item>
        <el-form-item label="主任务名：" prop="m_name">
          <el-input v-model="addForm.m_name"></el-input>
        </el-form-item>
        <el-form-item label="从任务ID：" prop="s_id">
          <el-input v-model="addForm.s_id"></el-input>
        </el-form-item>
        <el-form-item label="从任务名：" prop="s_name">
          <el-input v-model="addForm.s_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取消</el-button>
        <el-button type="primary" @click="addServer">确定</el-button>
      </span>
    </el-dialog>
        <!-- 删除提示框 -->
        <el-dialog title="提示" :visible.sync="delVisible" width="300px" center>
            <div class="del-dialog-cnt">是否删除选择项</div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="cancel">取消</el-button>
                <el-button type="primary" @click="DeleteTask" v-loading.fullscreen.lock="fullscreenLoading">确定</el-button>
            </span>
        </el-dialog>
    </div>
  </el-card>

</template>

<script>
export default {
  data () {
    return {
      fullscreenLoading: false,
      loading: true,
      tasklist: [],
      total: 0,
      queryinfo: {
        page: 1,
        page_size: 10
      },
      dialogVisible: false,
      editdialogVisible: false,
      addForm: {
        m_name: null,
        m_id: null,
        s_name: null,
        s_id: null
      },
      addFormRule: {
        m_id: [
          { required: true, message: '请输入主任务ID', trigger: 'blur' }],
        m_name: [
          { required: true, message: '请输入主任务名', trigger: 'blur' }],
        s_id: [
          { required: true, message: '请输入从任务ID', trigger: 'blur' }],
        s_name: [
          { required: true, message: '请输入从任务名', trigger: 'blur' }]
      },
      delVisible: false,
      msg: '',
      ids: [],
      multipleSelection: [],
      fileList: [],
      currentPage: null,
      uacategorylist: []
    }
  },
  created () {
    this.getualist()
  },
  methods: {
    // 搜索重置页面
    search () {
      this.queryinfo.page = 1
      this.currentPage = 1
      this.getualist()
    },
    // 获取ua列表数据
    async getualist () {
      this.loading = true
      const res = await this.$http.get('campaign_mapping')
      if (res.data.ret_code !== 200) return this.$message.error('查询信息失败!')
      console.log(res)
      this.tasklist = res.data.ret_data
      this.total = res.data.ret_data.total_count
      this.loading = false
    },
    // 提交添加表单
    async addServer () {
        console.log(this.addForm)
        const res = await this.$http.post('campaign_mapping', this.addForm)
        if (res.data.ret_code !== 200) return this.$message.error('提交表单失败!')
        this.dialogVisible = false
        this.getualist()
        this.$message.success('添加成功!')
    },
    // 换页重新获取数据
    handleCurrentChange (newpage) {
      this.loading = true
      this.queryinfo.page = newpage
      this.getualist()
    },
    // 换每页显示条数
    handleSizeChange (newsize) {
      this.loading = true
      this.queryinfo.page_size = newsize
      this.getualist()
    },
    // 关闭对话框
    dialogClose () {
      this.$refs.addFormRef.resetFields()
    },
    // 删除确认框
    dele (code) {
        this.$confirm('是否删除选择项？', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.DeleteTask(code)
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '取消删除'
          })
        })
      },
    // 删除按钮
    async DeleteTask (code) {
      console.log(code)
      this.loading = true
      const res = await this.$http.delete('campaign_mapping?userId=' + code)
      if (res.data.ret_code !== 200) return this.$message.error('删除失败!')
      this.$message.success('删除成功!')
      // 当删除页面最后一条数据时跳转到上一页
      const totalPage = Math.ceil((this.total - 1) / this.queryinfo.page_size) // 总页数
      this.queryinfo.page = this.queryinfo.page > totalPage ? totalPage : this.queryinfo.page
      this.queryinfo.page = this.queryinfo.page < 1 ? 1 : this.queryinfo.page
      this.getualist()
    },
    // Batch delete
    delAll () {
        this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
        })
        .then(async () => {
          for (let i = 0; i < this.multipleSelection.length; i++) {
            const res = await this.$http.delete('campaign_mapping?userId=' + this.multipleSelection[i].id)
            if (res.data.ret_code !== 200) return this.$message.error('删除失败!')
          }
          this.$message.success('删除成功!')
          this.getualist()
        })
    },
    // 操作多选
    handleSelectionChange (val) {
      console.log(val)
      this.multipleSelection = val
    },
    // 取消多项删除
    cancel () {
      this.delVisible = false
      this.ids = []
    },
    // 刷新页面
    refresh () {
      this.queryinfo = {
        page: 1,
        page_size: 10
      }
      this.search()
    }
  }
}
</script>

<style lang="less" scoped>
.el-table {
  margin-top: 20px;
}
.el-pagination {
  margin-top: 20px;
  margin-bottom: 26px;
}
.upload-demo {
    display: inline;
    padding-left: 10px;
}
.el-upload__tip {
    margin-left: 470px;
}
</style>
