<template>
  <el-card class="box-card">
    <div style="margin-top: 5px;">
      <el-row :gutter="20" class="elrow">
        <el-col :span="20">
          <span>请选择任务：</span>
          <el-select v-model="id" placeholder="请选择" @change="tasknamechange" filterable style="width:50%">
            <el-option
              v-for="item in options"
              :key="item.id"
              :label="item.m_name"
              :value="item.id">
            </el-option>
          </el-select>
        <el-button type="primary" class="URLbutton" @click="refreshtask" :disabled='isname'>刷新任务</el-button>
        </el-col>
      </el-row>
    <el-row :gutter="20" class="elrow">
      <el-col :span="20">
        <el-input v-model="teakURL" placeholder="任务URL" style="width: 800px;" :disabled="true"  id="link"></el-input>
        <el-button type="primary" class="URLbutton" :disabled='isUrl' @click="getURL">获取URL</el-button>
        <el-button type="primary" class="URLbutton" id="copybtn" v-clipboard:copy="teakURL" v-clipboard:success="onCopy" v-clipboard:error="onError">复制URL</el-button>
      </el-col>
    </el-row>
  </div>
  </el-card>

</template>

<script>
export default {
  data () {
    return {
      options: [],
      s_taskid: '',
      m_taskid: '',
      teakURL: '',
      isUrl: true,
      isname: true,
      id: ''
    }
  },
  created () {
    this.gettaskname()
  },
  methods: {
    async gettaskname () {
      this.isname = true
      const res = await this.$http.get('campaign_mapping')
      this.options = res.data.ret_data
      this.isname = false
      console.log(this.options)
    },
    tasknamechange (value) {
      this.isUrl = false
      const rule = this.options.find(item => item.id === value)
      this.m_taskid = rule.m_id
      this.s_taskid = rule.s_id
      console.log(rule)
    },
    async getURL () {
      this.isname = true
      this.isUrl = true
      const res = await this.$http.get('campaign_site_url/' + this.m_taskid + '/' + this.s_taskid)
      this.teakURL = res.data.ret_data
      this.isname = false
      this.isUrl = false
      console.log(this.teakURL)
    },
    onCopy () {
      this.$message({
          message: '复制成功！',
          type: 'success'
        })
    },
    onError () {
      this.$message.error('复制失败！')
    },
    refreshtask () {
      this.gettaskname()
    }
  }
}
</script>

<style lang="less" scoped>
.URLbutton {
  margin-left: 15px;
}
.elrow {
  margin-bottom: 25px;
}
</style>