import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/Home.vue'
import Taskurl from '../components/Taskurl.vue'
import welcome from '../components/welcome.vue'
import TaskBinding from '../components/TaskBinding.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/home',
        component: Home,
        redirect: '/welcome',
        children: [{
            path: '/Taskurl',
            component: Taskurl
        }, {
            path: '/welcome',
            component: welcome
        }, {
            path: '/TaskBinding',
            component: TaskBinding
        }]
    },
    {
        path: '/',
        redirect: '/Taskurl'
    }
    // {
    //     path: '/login',
    //     component: Login
    // },
    // {
    //     path: '/401',
    //     component: error401
    // }
]

const router = new VueRouter({
    routes
})

